import Link from 'next/link';
import styled from 'styled-components';

const StyledButton = styled.button`
  width: 100%;
  padding: 12px;
  border-radius: ${props => props.theme.border_radius} / 2;
  font-size: ${props => props.theme.xs};
  font-weight: bold;

  &.submit,
  &.primary {
    background: ${props => props.theme.blue};
    color: ${props => props.theme.white};
  }

  &:disabled {
    cursor: not-allowed;
  }

  &.disabled {
    color: ${props => props.theme.disabled_text};
    background: ${props => props.theme.disabled_bg};
  }
`;

const StyledLink = styled.a`
  width: 100%;
  padding: 12px;
  border-radius: ${props => props.theme.border_radius} / 2;
  font-size: ${props => props.theme.xs};
  font-weight: bold;

  &.submit,
  &.primary {
    background: $c-blue;
    color: ${props => props.theme.white};
  }

  &:disabled {
    cursor: not-allowed;
  }

  &.disabled {
    color: ${props => props.theme.disabled_text};
    background: $c-disabled-bg;
  }
`;

const Button = props => {
  const { as, handleClick, href, variant } = props;

  if (as === 'link') {
    return (
      <Link href={href}>
        <StyledLink className={variant}>
          {props.children}
        </StyledLink>
      </Link>
    ); 
  }

  return (
    <StyledButton onClick={handleClick}>
      {props.children}
    </StyledButton>
  );
}

export default Button;